var express = require('express');
var app = express();
var $ = require('jQuery');

//var instagramAPI = require('./src/instagram-api/instagram-api');

var accessToken = '2733309.11c398a.82323294a73e4a919981e3b2b6bf4acc';
var InstagramAPI = require('instagram-api');
var instagramAPI = new InstagramAPI(accessToken);

app.get('/home', function(req, res) {
  instagramAPI.userSelfMedia(5).then(function(result) {
    //console.log(result.data.slice()[0]); // user info
    console.log(result.data.slice()[0]);
    res.render('HomePage.ejs', {imgList: result.data});
  }, function(err) {
    console.log(err); // error info
  });

})

app.use(function(req, res, next) {
  res.setHeader('Content-Type', 'text/plain');
  res.status(404).send('Page introuvable !');
});

var server = app.listen(8081, function() {
  var host = server.address().address
  var port = server.address().port

  console.log("Example app listening at http://%s:%s", host, port)
})



